import { Product } from '@core/models/product.model'

export class Cart {
  uuid?: string
  total: number
  products: Array<CartProduct>
  currency?: string
  costDelivery?: number
  numberOfProducts: number
  estimatedDelivery?: string

  static fromDto(attrs?: Partial<Cart>) {
    const cart = new Cart()
    cart.total = attrs?.total!
    cart.currency = attrs?.currency
    cart.costDelivery = attrs?.costDelivery
    cart.estimatedDelivery = attrs?.estimatedDelivery
    cart.numberOfProducts = attrs?.numberOfProducts ?? 0
    cart.products = attrs?.products?.map(CartProduct.fromCartDto) || []

    return cart
  }
}

export class CartProduct extends Product {
  quantity: number

  // id: number
  // imageUrl: string
  // name: string
  // stockType: string
  // priceWithVat: number
  // seoUrl: string
  // randomProductId: string

  static fromCartDto(attrs: Partial<CartProduct>|any) {
    const product = new CartProduct(attrs)
    product.id = attrs.id!
    product.name = attrs.name!
    product.randomProductId = attrs.randomProductId!
    product.quantity = attrs.quantity
    // @todo
    product.stockType = { id: 0, name: attrs.stockType, type: attrs.stockType }

    // product.seoUrl = attrs.seoUrl
    // product.imageUrl = attrs.imageUrl

    product.images = [{ url: attrs.imageUrl!, id: 0 }]
    product.price = { priceWithVat: attrs.priceWithVat, priceWithoutVat: attrs.priceWithVat }
    product.seo = { metaUrl: attrs.metaUrl! }

    return product
  }
}
