import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core'

@Component({
  selector: 'app-rating',
  templateUrl: './rating.component.html',
  styleUrls: ['./rating.component.scss']
})
export class RatingComponent implements OnInit {

  @Input()
  public total = 5

  @Input()
  public fontSize = 0

  @Input()
  public inline = false

  @Input()
  public ratable = false

  @Input()
  set rating(value: string|number) {
    [this._rating.whole, this._rating.fractional] = String(value).split('.')
  }

  @Output()
  public rate: EventEmitter<number> = new EventEmitter<number>()

  // eslint-disable-next-line @typescript-eslint/member-ordering
  public _rating = { whole: '', fractional: '' }

  ngOnInit(): void {
  }

  onRated(rate: number) {
    if (this.ratable) {
      this.rating = rate
      this.rate.emit(rate)
    }
  }
}
