import { Injectable } from '@angular/core'
import { BehaviorSubject, finalize, Observable, tap } from 'rxjs'
import { Cart, CartProduct } from '@core/models/cart.model'
import { ApiService } from '@core/services/api.service'
import { MicroserviceType } from '@core/enums/microservice-type.enum'
import { Product } from '@core/models/product.model'
import { LocalStorageService } from '@core/services/local-storage.service'

@Injectable({
  providedIn: 'root',
})
export class CartService {
  private cartSource: BehaviorSubject<Cart> = new BehaviorSubject(undefined as any)

  private cartRequestPending = false

  private cart: Observable<Cart> = this.cartSource.asObservable()

  constructor(private api: ApiService, private localStorage: LocalStorageService) {}

  private request(method: string, api: string, body?: { id: number, quantity: number }) {
    const headers = this.localStorage.cartUuid ? { uuid: this.localStorage.cartUuid } : {}
    return this.api.request(method, api, MicroserviceType.inv, { body, headers })
  }

  fetchCart() {

    if (! this.localStorage.cartUuid && ! this.localStorage.token) {
      this.cartSource.next(Cart.fromDto({ total: 0 }))
      return
    }

    this.cartRequestPending = true
    this.request('get', 'cart')
      .pipe(finalize(() => this.cartRequestPending = false))
      .subscribe(cart => {
        cart = Cart.fromDto(cart)
        this.cartSource.next(cart)
      })
  }

  emptyCart() {
    this.localStorage.cartUuid = null
    this.cartSource.next(Cart.fromDto({ total: 0 }))
  }

  addToCart(product: Product, quantity: number) {
    return this.request('post', 'cart', { id: product.id, quantity })
      .pipe(tap((cart: Cart) => {
        this.localStorage.cartUuid = cart.uuid ?? null
        this.cartSource.next(Cart.fromDto(cart))
      }))
  }

  updateQty(product: CartProduct, quantity: number) {
    return this.request('put', 'cart', { id: product.id, quantity })
      .pipe(tap((cart: Cart) => this.cartSource.next(Cart.fromDto(cart))))
  }

  getCart() {
    if (this.cartSource.value === undefined && !this.cartRequestPending) {
      this.fetchCart()
    }

    return this.cart
  }

  removeProduct(product: CartProduct) {
    return this.request('delete', `cart/${product.id}`)
      .pipe(tap((cart: Cart) => this.cartSource.next(Cart.fromDto(cart))))
  }
}
