import { User } from '@core/models/user.model'
import { ProductCategory } from '@core/models/types'
import { CartProduct } from '@core/models/cart.model'

export class Product {
  id: number
  name: string
  slug: string
  favorite?: boolean
  price: { priceWithoutVat: number, priceWithVat: number}
  monthlyPrice?: number
  discount?: number
  priceDiscount?: Discount
  condition: { id: number, name: string, type: Condition }
  guarantee?: number
  randomProductId: string
  returnPolicy?: number
  currency: string
  seo: { metaUrl: string }
  stockType?: { id: number, name: string, type: StockTypeEnum, startDatePreorder?: Date }
  stock: number
  // startDatePreorder?: Date
  delivery?: ProductDelivery
  sizes?: Array<ProductSize>
  images: Array<ProductImage>
  primaryCategory: ProductCategory
  description: string
  history?: string
  vidDescr?: string
  docDescr?: string
  reviewsCount?: number
  rating?: ProductRating
  variationAttributes?: VariationAttributes
  parent?: Product
  variations?: Array<Product>
  specs: Array<ProductSpec>

  constructor(product: Product) {
    this.id = product.id
    this.name = product.name
    this.slug = product.slug
    this.favorite = product.favorite
    this.price = product.price
    this.monthlyPrice = product.monthlyPrice
    this.discount = product.discount
    this.priceDiscount = product.priceDiscount
    this.condition = product.condition
    this.guarantee = product.guarantee
    this.randomProductId = product.randomProductId
    this.returnPolicy = product.returnPolicy
    this.currency = product.currency
    this.seo = product.seo
    this.stockType = product.stockType
    this.stock = product.stock
    // this.startDatePreorder = product.startDatePreorder
    this.delivery = product.delivery
    this.sizes = product.sizes
    this.images = product.images
    this.primaryCategory = product.primaryCategory
    this.description = product.description
    this.history = product.history
    this.vidDescr = product.vidDescr
    this.docDescr = product.docDescr
    this.reviewsCount = product.reviewsCount
    this.rating = product.rating
    this.variationAttributes = product.variationAttributes
    this.parent = product.parent
    this.variations = product.variations
    this.specs = product.specs
  }

  static fromDto(productDto: DtoProduct): Product {

    const product: Product = {
      ...productDto,
      specs: DtoProduct.formatTechnicalSpecifications(productDto),
      variationAttributes: DtoProduct.formatVariationAttributes(productDto),
      // startDatePreorder: productDto.startDatePreorder ? new Date(productDto.startDatePreorder) : undefined,
      stockType: undefined,
      parent: productDto.parent ? Product.fromDto(productDto.parent) : undefined,
      variations: productDto.variations ? productDto.variations.map(Product.fromDto) : undefined
    }

    const stockType = productDto.stockType?.stockType

    if (stockType) {
      let startDatePreorder = productDto.stockType?.preorder?.startDatePreorder
      startDatePreorder = startDatePreorder ? new Date(startDatePreorder) : undefined
      product.stockType = { ...stockType, startDatePreorder }
    }

    return new Product(product)
  }
}

class DtoProduct {
  id: number
  name: string
  slug: string
  favorite?: boolean
  price: { priceWithoutVat: number, priceWithVat: number}
  monthlyPrice?: number
  discount?: number
  priceDiscount?: Discount
  condition: { id: number, name: string, type: Condition }
  guarantee?: number
  randomProductId: string
  returnPolicy?: number
  currency: string
  seo: { metaUrl: string }
  stockType?: {
    stockType: { id: number, name: string, type: StockTypeEnum },
    preorder?: { startDatePreorder?: Date }
  }
  stock: number
  startDatePreorder?: Date
  delivery?: ProductDelivery
  sizes?: Array<ProductSize>
  images: Array<ProductImage>
  attrs?: Array<ProductAttr>
  primaryCategory: ProductCategory
  colors?: Array<string>
  description: string
  history?: string
  vidDescr?: string
  docDescr?: string
  reviewsCount?: number
  rating?: ProductRating
  parent?: DtoProduct
  variations?: Array<DtoProduct>
  specs: Array<ProductSpec>
  technicalSpecificationAttributes?: Array<TechnicalSpecification>
  groupsTechnicalSpecificationAttributes?: Array<TechnicalSpecificationGroup>
  variationAttributes: Array<VariationAttribute>

  static formatTechnicalSpecifications(product: DtoProduct): Array<ProductSpec> {

    const characteristics: Array<ProductSpec> = []

    product.technicalSpecificationAttributes?.forEach((techSpec: TechnicalSpecification) => {
      characteristics?.push({
        label: techSpec.attribute.name,
        value: techSpec.technicalSpecificationValues.map(spec => spec.attributeValue.name).join(', ')
      })
    })

    product.groupsTechnicalSpecificationAttributes?.forEach((techSpecs: TechnicalSpecificationGroup) => {
      techSpecs.attributeSet.forEach(techSpec => {
        characteristics?.push({
          label: techSpec.attribute.name,
          value: techSpec.technicalSpecificationValues.map(spec => spec.attributeValue.name).join(', ')
        })
      })
    })

    return characteristics
  }

  static formatVariationAttributes(attrs?: DtoProduct): VariationAttributes|undefined {

    if (! attrs?.variationAttributes) {
      return undefined
    }

    const keyedVariations: Partial<VariationAttributes> = {}

    attrs?.variationAttributes?.forEach((attr: VariationAttribute) => {
      const attrName = attr.attribute.name as keyof VariationAttributes
      keyedVariations[attrName] = attr
    })

    return keyedVariations as VariationAttributes
  }

}

export type VariationAttributeValue = {
  attributeValue: { id: number, name: string }
}

export enum Condition {
  U = 'USED',
  N = 'NEW'
}

type Discount = {
  dataStart: string,
  dateEnd: string,
  discountedValue: number,
  oldPriceWithoutVat: number
}

type VariationAttribute = {
  attribute: { id: number, name: string }
  variationValues: Array<VariationAttributeValue>
}

export type VariationAttributes = {
  Size: VariationAttribute
  Color: VariationAttribute
}

class ProductSize {
  label: string
  available: boolean

  constructor(label: ProductSize) {
    this.label = label.label
    this.available = label.available
  }
}

export type ProductDelivery = {
  method: string
  time: string
  price: number
}

export type ProductAttr = {
  label: string
  value: string
}

export type ProductReview = {
  reviewedAt: string
  rating: number
  message: string
  customer: User
}

export class ProductImage {
  /**
   * @todo are we gonna use same imanges for thumbnail and jumbo imsages?
   */
  jumbo?: string
  thumbnail?: string
  id: number
  url: string

  constructor(image: ProductImage) {
    this.id = image.id
    this.url = image.url
  }
}

export enum StockTypeEnum {
  IN_STOCK = 'IN_STOCK',
  OUT_OF_STOCK = 'OUT_OF_STOCK',
  PREORDER = 'PREORDER',
  NOT_AVAILABLE = 'NOT_AVAILABLE',
  LIMITED_STOCK = 'LIMITED_STOCK'
}

type ProductRating = {
  productCount: number
  productRating: number
}

class TechnicalSpecification {

  attribute: {id: number, name: string}
  technicalSpecificationValues: [{attributeValue: {id: number, name: string}}]

  constructor(techSpecification: TechnicalSpecification) {
    this.attribute = techSpecification.attribute
    this.technicalSpecificationValues = techSpecification.technicalSpecificationValues
  }
}

class TechnicalSpecificationGroup {
  id: number
  name: string
  attributeSet: Array<TechnicalSpecification>

  constructor(techSpecGroup: TechnicalSpecificationGroup) {
    this.id = techSpecGroup.id
    this.name = techSpecGroup.name
    this.attributeSet = techSpecGroup.attributeSet
  }
}

export type ProductSpec = {
  label: string
  value: string
}
