import { Component, Input, OnInit, ViewChild } from '@angular/core'
import { AutoDestroy } from '@core/common/auto-destroy'
import { ApiService } from '@core/services/api.service'
import { takeUntil } from 'rxjs'
import { BreakpointObserver } from '@angular/cdk/layout'
import { AuthUserService } from '@core/services/auth-user.service'
import { ActivatedRoute, Router } from '@angular/router'
import { Location } from '@angular/common'
import { User } from '@core/models/user.model'
import { LocalStorageService } from '@core/services/local-storage.service'
import { MatLegacyMenuTrigger as MatMenuTrigger } from '@angular/material/legacy-menu'
import { MicroserviceType } from '@core/enums/microservice-type.enum'
import { environment } from 'src/environments/environment';

@Component({
  selector: 'app-main-layout',
  templateUrl: './main-layout.component.html',
  styleUrls: ['./main-layout.component.scss']
})
export class MainLayoutComponent  extends AutoDestroy implements OnInit {

  @Input()
  public showDesktopSideNav = false

  @Input()
  public showTopbarLinks = true

  @ViewChild(MatMenuTrigger) mainTrigger: MatMenuTrigger

  public categories: any
  public user?: User|null
  public pageLabel: string
  public isMobile: boolean
  public activeStore?: string
  public storeLinks = [
    {label: "192.168.0.182", value: "http://192.168.0.182"},
    {label: "192.168.0.182:89", value: "http://192.168.0.182:89"}
  ]

  public constructor(
    private breakpointObserver: BreakpointObserver,
    private api: ApiService,
    private localStorage: LocalStorageService,
    private authService: AuthUserService,
    private router: Router,
    private route: ActivatedRoute,
    private location: Location
  ) {
    super()
    this.pageLabel = route.snapshot.data['label']
    this.activeStore = environment.lunaServiceUrl
  }

  public ngOnInit(): void {
    this.breakpointObserver
      .observe(['(min-width: 992px)'])
      .subscribe((state) => this.isMobile = !state.matches)

    this.authService.getAuthUser().subscribe((user) => this.user = user)

    this.fetchCategories()
  }

  fetchCategories() {
    this.api.get('filter/categories', MicroserviceType.inv)
      .pipe(takeUntil(this.destroy$))
      .subscribe((data) => {
        this.categories = data?.filters?.categories ?? []
      })
  }

  logout() {
    this.api.post('logout', {})
      .subscribe(() => {
        this.authService.logoutAuthUser()
        this.router.navigate(['/'])
      })

  }

  back() {
    this.location.back()
  }

  visitStore() {
    if (this.activeStore) {
      window.location.href = this.activeStore
    }
  }
}
