import { Component, EventEmitter, Input, Output } from '@angular/core'
import { MatLegacyFormFieldAppearance as MatFormFieldAppearance } from '@angular/material/legacy-form-field'

@Component({
  selector: 'app-input',
  templateUrl: 'input.component.html',
  styleUrls: ['input.component.scss']
})
export class InputComponent {
  @Input()
  public name: string

  @Input()
  public type: string = 'text'

  @Input()
  public control: any

  @Input()
  public label: string

  @Input()
  public placeholder: string

  @Input()
  public hideRequiredMarker: boolean = true

  @Input()
  public appearance: MatFormFieldAppearance = 'fill'

  @Input()
  public suffix: string

  @Input()
  public prefix: string

  @Input()
  public borderColor: string

  @Input()
  public height: string

  @Output()
  public suffixClick = new EventEmitter<number>()

  @Output()
  public prefixClick = new EventEmitter<number>()

  public errors: Array<string>

  public hide: boolean = true

  _suffixClick() {
    this.suffixClick.emit()
  }

  _prefClick() {
    this.prefixClick.emit()
  }
}
