import { Component, EventEmitter, Input, Output } from '@angular/core'
import { Product, Condition } from '@core/models/product.model'
import { ApiService } from '@core/services/api.service'
import { AutoDestroy } from '@core/common/auto-destroy'
import { takeUntil } from 'rxjs'
import { CartProduct } from '@core/models/cart.model'

@Component({
  selector: 'app-product-thumbnail',
  templateUrl: './product-thumbnail.component.html',
  styleUrls: ['./product-thumbnail.component.scss']
})
export class ProductThumbnailComponent extends AutoDestroy {

  @Input()
  public product: Product|CartProduct

  @Input()
  public viewType: 'list' | 'grid' = 'list'

  @Input()
  public showColors = false

  @Input()
  public showCartBtn = false

  @Output()
  public addToCart = new EventEmitter<any>()

  readonly Condition = Condition

  constructor(private api: ApiService) {
    super()
  }

  toggleFavourite(product: Product) {
    product.favorite = !product.favorite

    this.api.put(`product/${product.id}/toggle-favorite`, {})
      .pipe(takeUntil(this.destroy$))
      .subscribe()
  }

  public _addToCart(product: Product) {
    this.addToCart.emit()
  }
}
