import { Component, Input } from '@angular/core'
import { getCurrencySymbol as _getCurrencySymbol } from '@angular/common'

@Component({
  selector: 'app-price',
  templateUrl: './price.component.html',
  styleUrls: ['./price.component.scss']
})
export class PriceComponent {

  @Input()
  public lineThrough = false

  @Input()
  public currency?: string

  public _amount = { whole: '', fractional: '' }
  @Input()
  set amount(value: string|number|undefined) {
    [this._amount.whole, this._amount.fractional] = String(value).split('.')
    this._amount.fractional = this._amount.fractional?.substring(0, 3)
  }

  getCurrencySymbol(code: string) {
    return _getCurrencySymbol(code, 'narrow')
  }
}
