import { Component, Input, OnInit } from '@angular/core'
import { LoadingService } from '@core/services/loading.service'
import { CartService } from '@core/services/cart.service'
import { NavigationEnd, NavigationStart, Router, Event as NavigationEven } from '@angular/router'

@Component({
  selector: 'app-toolbar',
  templateUrl: './toolbar.component.html',
  styleUrls: ['./toolbar.component.scss']
})
export class ToolbarComponent implements OnInit {

  @Input()
  public sidenav: any

  @Input()
  public mobileOnly: boolean = false

  loading$ = this.loader.loading$

  cartCount: number|null = null

  constructor(public loader: LoadingService, private cart: CartService, router: Router) {

    router.events.subscribe(
      (event: NavigationEven): void => {
        if (event instanceof NavigationStart) {
          this.loader.increment()
        } else if (event instanceof NavigationEnd) {
          this.loader.decrement()
        }
      }
    )
  }

  ngOnInit(): void {
    this.cart.getCart().subscribe(cart => {
      if (cart) {
        this.cartCount = cart.products.reduce((carry, product) => product.quantity + carry, 0)
      }
    })
  }

}
